import React, { Component } from "react";
import NewsItem from "./NewsComponent/NewsItem";

export default class News extends Component {
  articals = [
    {
      source: {
        id: "bbc-sport",
        name: "BBC Sport",
      },
      author: "BBC Sport",
      title: "Hiding in Kabul - the cricket players who fear for their lives",
      description:
        "Members of Afghanistan's women's cricket team are hiding in Kabul, fearing for their safety following the Taliban's return to power.",
      url: "http://www.bbc.co.uk/sport/cricket/58396310",
      urlToImage:
        "https://ichef.bbci.co.uk/live-experience/cps/624/cpsprodpb/82FE/production/_120343533_kabul_fighter_gettyin.jpg",
      publishedAt: "2021-09-01T23:22:25.6012262Z",
      content:
        "A Taliban fighter walks past a beauty salon with defaced images of women in Kabul Asel and many of her international team-mates are in hiding. Asel isn't her real name. In Kabul members of the Talib… [+7910 chars]",
    },
    {
      source: {
        id: "espn-cric-info",
        name: "ESPN Cric Info",
      },
      author: null,
      title:
        "PCB hands Umar Akmal three-year ban from all cricket | ESPNcricinfo.com",
      description:
        "Penalty after the batsman pleaded guilty to not reporting corrupt approaches | ESPNcricinfo.com",
      url: "http://www.espncricinfo.com/story/_/id/29103103/pcb-hands-umar-akmal-three-year-ban-all-cricket",
      urlToImage:
        "https://a4.espncdn.com/combiner/i?img=%2Fi%2Fcricket%2Fcricinfo%2F1099495_800x450.jpg",
      publishedAt: "2020-04-27T11:41:47Z",
      content:
        "Umar Akmal's troubled cricket career has hit its biggest roadblock yet, with the PCB handing him a ban from all representative cricket for three years after he pleaded guilty of failing to report det… [+1506 chars]",
    },
    {
      source: {
        id: "espn-cric-info",
        name: "ESPN Cric Info",
      },
      author: null,
      title:
        "What we learned from watching the 1992 World Cup final in full again | ESPNcricinfo.com",
      description:
        "Wides, lbw calls, swing - plenty of things were different in white-ball cricket back then | ESPNcricinfo.com",
      url: "http://www.espncricinfo.com/story/_/id/28970907/learned-watching-1992-world-cup-final-full-again",
      urlToImage:
        "https://a4.espncdn.com/combiner/i?img=%2Fi%2Fcricket%2Fcricinfo%2F1219926_1296x729.jpg",
      publishedAt: "2020-03-30T15:26:05Z",
      content:
        "Last week, we at ESPNcricinfo did something we have been thinking of doing for eight years now: pretend-live ball-by-ball commentary for a classic cricket match. We knew the result, yes, but we tried… [+6823 chars]",
    },
  ];
  constructor() {
    super();
    this.state = {
      artical: this.articals,
    };

    // console.log(this.state.artical[0].);
  }

  render() {
    return (
      <div>
        <h1>News conponent</h1>
        <div className="row">
          {this.state.artical.map((e) => {
            return (
              <div className="container  col-md-3" key={e.url}>
                <NewsItem
                  title={
                    e.title.length > 42
                      ? e.title.slice(0, 42) + ` ...`
                      : e.title
                  }
                  descriptions={
                    e.description.length > 88
                      ? e.description.slice(0, 85) + `  .....`
                      : e.description
                  }
                  image={e.urlToImage}
                  newsUrl={e.url}
                />
              </div>
            );
          })}
          {/* <div className="container  col-md-3">
            <NewsItem title={"title2"} discription={"discription 2"} />
          </div>
          <div className="container  col-md-3">
            <NewsItem title={"title3"} discription={"discription 3"} />
          </div>
          <div className="container  col-md-3">
            <NewsItem title={"title4"} discription={"discription 4"} />
          </div> */}
        </div>
      </div>
    );
  }
}
